import i18n_en from '../../emoji-mart-data/i18n/en.json'
import data_default from '../../emoji-mart-data/sets/4/apple.json'
import { FrequentlyUsed } from './helpers'

export let I18n = i18n_en
export let Data = data_default

const DEFAULT_PROPS = {
  autoFocus: {
    value: false,
  },
  emojiButtonColors: {
    value: null,
  },
  emojiButtonRadius: {
    value: '100%',
  },
  emojiButtonSize: {
    value: 34,
  },
  emojiSize: {
    value: 22,
  },
  emojiVersion: {
    value: 14,
    choices: [1, 2, 3, 4, 5, 11, 12, 12.1, 13, 13.1, 14],
  },
  locale: {
    value: 'en',
    choices: ['en', 'fr'],
  },
  maxFrequentRows: {
    value: 4,
  },
  navPosition: {
    value: 'top',
    choices: ['top', 'bottom', 'none'],
  },
  noCountryFlags: {
    value: false,
  },
  noResultsEmoji: {
    value: null,
  },
  perLine: {
    value: 9,
  },
  previewEmoji: {
    value: null,
  },
  previewPosition: {
    value: 'bottom',
    choices: ['top', 'bottom', 'none'],
  },
  set: {
    value: 'native',
    choices: ['native', 'apple', 'facebook', 'google', 'twitter'],
  },
  skin: {
    value: 1,
    choices: [1, 2, 3, 4, 5, 6],
  },
  stickySearch: {
    value: true,
  },
  theme: {
    value: 'auto',
    choices: ['auto', 'light', 'dark'],
  },
}

async function fetchJSON(src) {
  const response = await fetch(src)
  return await response.json()
}

let promise = null
let initiated = false
let initCallback = null
export function init(options) {
  promise ||
    (promise = new Promise((resolve) => {
      initCallback = resolve
    }))

  if (!initiated) {
    initiated = true
    _init(options || {})
  }

  return promise
}

function _init(props, element) {
  // const { i18n } = props
  console.log('INITING')
  const pickerProps = getProps(props, element)
  // const { locale } = pickerProps

  // I18n = i18n) ||
  //   (locale == 'en'
  //     ? i18n_en
  //     : await fetchJSON(
  //         `https://cdn.jsdelivr.net/npm/@emoji-mart/data@latest/i18n/${locale}.json`,
  //       ))

  if (pickerProps.maxFrequentRows) {
    const emojis = FrequentlyUsed.get(pickerProps)
    if (emojis.length) {
      Data.categories.unshift({
        id: 'frequent',
        emojis: emojis,
      })
    }
  }

  if (props.categories) {
    console.log('props.catgeories', props.categories)
    console.log('data catgeories', Data.categories)
    Data.categories = Data.categories
      .filter((c) => {
        return props.categories.indexOf(c.id) != -1
      })
      .sort((c1, c2) => {
        const i1 = props.categories.indexOf(c1.id)
        const i2 = props.categories.indexOf(c2.id)

        return i1 - i2
      })
  }

  let noCountryFlags = null

  Data.natives = {}
  for (const category of Data.categories) {
    console.log('category', category)
    let i = category.emojis.length

    const { categoryIcons } = props
    if (categoryIcons) {
      const icon = categoryIcons[category.id]
      if (icon && !category.icon) {
        category.icon = icon
      }
    }

    while (i--) {
      const emoji = Data.emojis[category.emojis[i]]

      console.log()
      if (!emoji) {
        continue
      }

      emoji.search =
        ',' +
        [
          [emoji.id, false],
          [emoji.name, true],
          // TODO: once we load in the emoji data asynchronously, we can add back keyword support.
          // we do this because we want to reduce the bundle size for initial page load.
          // [emoji.keywords, false],
        ]
          .map(([strings, split]) => {
            if (!strings) return
            return (Array.isArray(strings) ? strings : [strings])
              .map((string) => {
                return (split ? string.split(/[-|_|\s]+/) : [string]).map((s) =>
                  s.toLowerCase(),
                )
              })
              .flat()
          })
          .flat()
          .filter((a) => a && a.trim())
          .join(',')

      let skinIndex = 0
      for (const skin of emoji.skins) {
        if (!skin) continue
        skinIndex++

        // const { native } = skin
        // if (native) {
        //   Data.natives[native] = emoji.id
        //   emoji.search += `,${native}`
        // }

        const skinShortcodes = skinIndex == 1 ? '' : `:skin-tone-${skinIndex}:`
        skin.shortcodes = `:${emoji.id}:${skinShortcodes}`
      }
      console.log('emoji search', emoji.search)
    }
  }

  for (const alias in Data.aliases) {
    const emojiId = Data.aliases[alias]
    const emoji = Data.emojis[emojiId]
    if (!emoji) continue

    emoji.aliases || (emoji.aliases = [])
    emoji.aliases.push(alias)
  }

  initCallback(pickerProps)
}

function getProps(props, element) {
  props || (props = {})

  function get(propName) {
    const defaults = DEFAULT_PROPS[propName]
    let value = (element && element.getAttribute(propName)) || props[propName]

    if (
      value != null &&
      defaults.value &&
      typeof defaults.value != typeof value
    ) {
      if (typeof defaults.value == 'boolean') {
        value = value == 'false' ? false : true
      } else {
        value = defaults.value.constructor(value)
      }
    }

    if (
      value == null ||
      (defaults.choices && defaults.choices.indexOf(value) == -1)
    ) {
      value = defaults.value
    }

    return value
  }

  const _props = {}
  for (let k in DEFAULT_PROPS) {
    _props[k] = get(k)
  }

  return _props
}
